.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px;
}

.card__img-container {
  justify-content: center;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
}

.card__title {
  margin: 40px 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  display: block;
}

.card__code {
  color: #616070;
  margin: 4px 0 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 16px;
  display: flex;
}

.card__review {
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.card__price-block {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-money {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:first-child, .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.body {
  color: #060b35;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}
